<template>
<main>
    <ImageModal v-if="modalVisible" @close="modalVisible = false" :imagedata="modalData"/>
    <ListModal v-if="modalVisible1" @close1="modalVisible1 = false" :listdata="modalData1"/>
    <Header :header="content"/>
    <h5 style="text-align:center;color:green;"> برای مثال کد 12345 وارد شود</h5>
    <div>
    <p>
        <input type="text" v-model="barcode" placeholder="کد قطعه مورد نظر را وارد نمایید ...">
        <button @click="getPart" class="btn btn-primary d-block mx-auto">دریافت</button>
    </p>
    </div>
    
    <h4 style="text-align:center;color:red;">{{error}}</h4>
    <table class="table   table-hover container" >
            <tbody>
                <tr>
                <th>نام</th>
                <td>{{partname}}</td>
                </tr>

                <tr>
                <th>برند</th>
                <td>{{brand}}</td>
                </tr>

                <tr>
                <th>تاریخ تولید</th>
                <td>{{gendate}}</td>
                </tr>

                <tr>
                <th>تاریخ انقضا</th>
                <td>{{exdate}}</td>
                </tr>

                <tr>
                <th>تاریخ خرید</th>
                <td>{{buydate}}</td>
                </tr>

                <tr>
                <th>دفعات استفاده</th>
                <td>{{usenum}}</td>
                </tr>

                <tr>
                <th>ست</th>
                <td>{{setname}}</td>
                </tr>

                <tr>
                <th>روش شستشو</th>
                <td>{{wash}}</td>
                </tr>

                <tr>
                <th>روش استریل</th>
                <td>{{sterille}}</td>
                </tr>

                <tr>
                <th>روش بسته بندی</th>
                <td>{{packing}}</td>
                </tr>

                <tr>
                <th>وضعیت ایستگاهی</th>
                <td>{{station}}</td>
                </tr>

                <tr>
                <th>وضعیت فیزیکی</th>
                <td>{{status}}</td>
                </tr>

                <tr>
                <th>عکس قطعه</th>
                <td><button   v-if="statuscode==200"   class="btn btn-success" @click="openModal()">نمایش</button></td>
                </tr>

                <tr>
                <th>آخرین وضعیت</th>
                <td><button   v-if="statuscode==200"   class="btn btn-success" @click="openModal1()">نمایش</button></td>
                </tr>

            </tbody>
    </table>
</main>
</template>

<script>
import axios from 'axios'
import Header from '../../components/Header.vue'
import ImageModal from '../../components/ImageModal.vue'
import ListModal from '../../components/ListModal.vue'
export default {
    name:'PartsView',
    components:{Header,ImageModal,ListModal},
    data() {
        return {
            modalVisible: false,
            modalVisible1: false,
            modalData: null,
            modalData1: null,
            content:'اطلاعات قطعه',
            partname:'',
            brand:'',
            gendate:'',
            exdate:'',
            buydate:'',
            usenum:'',
            setname:'',
            wash:'',
            sterille:'',
            packing:'',
            station:'',
            status:'',
            error:'',
            barcode:'',
            statuscode:''
        }
    },
    methods: {
        getPart(){
            if(this.barcode==='12345'){
            axios.get('../parts.json')
            .then((response)=>{
                this.partname=response.data.partname;
                this.brand=response.data.brand;
                this.gendate=response.data.gendate;
                this.exdate=response.data.exdate;
                this.buydate=response.data.buydate;
                this.usenum=response.data.usenum;
                this.setname=response.data.setname;
                this.wash=response.data.wash;
                this.sterille=response.data.sterille;
                this.packing=response.data.packing;
                this.station=response.data.station;
                this.status=response.data.status;
                this.modalData=response.data;
                this.modalData1=response.data;
                this.barcode='';
                this.error='';
                this.statuscode=response.status;
                })
            
            }else{
                this.error='کد قطعه اشتباه وارد شده است';
                this.barcode='';
            }
        },

        openModal() {
            
            this.modalVisible = true;
        },
        openModal1() {
            
            this.modalVisible1 = true;
        }
    },
}
</script>

<style lang="scss" scoped>
main{

    div{
        text-align: center;
            p{
                position: relative;
                display: inline-block;
                input{
                    
                    position:relative;
                    width: 30vw;
                    
                }
                button{
                    position: absolute;
                    left: 0;
                    top: 0;
                    bottom: 0;
                }

            }      

    }

    table{
        border-radius: 2px;
        background-color: rgb(255, 255, 255);
        width:80%;
        th{
            width: 15%;
        }
        td{
            text-align: center;
        }
    }
    
        

}
</style>